<template>
  <div class="bodyer">
    <div class='body-main'>
      <template>
        <el-table
          :data="tableData"
          v-loading="loading"
          header-cell-class-name="reg-header-cell"
          cell-class-name="reg-cell"
          stripe
          style="width: 100%">
          <el-table-column label="预约时间">
            <template slot-scope="scope">
              {{scope.row.ghrq}}
              <template v-if="scope.row.ysh_lx==1"> 上午</template>
              <template v-else> 下午</template>
            </template>
          </el-table-column>
          <el-table-column
            prop="mzh"
            label="门诊号">
          </el-table-column>
          <el-table-column
            prop="ysh"
            label="顺序号">
          </el-table-column>
          <el-table-column
            prop="ysxm"
            label="看诊医生">
          </el-table-column>
          <el-table-column
            prop="xm"
            label="就诊人">
          </el-table-column>
          <el-table-column label="挂号费">
            <template slot-scope="scope">
              ￥{{scope.row.ghf}}
            </template>
          </el-table-column>
          <el-table-column label="支付状态">
            <template slot-scope="scope">
                <template v-if="scope.row.sjh!=''">已在线支付</template>
                <template v-else>支付异常</template>
            </template>
          </el-table-column>
        </el-table>
      </template>
    </div>
    <div class='body-foot'>
      <el-button type="primary" @click="addReg">预约挂号</el-button>
      <el-button type="success" @click="showReport">检查报告</el-button>
    </div>
  </div>
</template>

<script>
import Cookies from 'js-cookie'
import config from "@/config/deploy";
import { post, get } from "@/api/index";
export default {
  inject: ['reload'],
  name: 'Bodyer',
  data() {
    return {
      loading: true,
      disabled: false,
      tableData: [],
    }
  },
  created: function() {
    if (Cookies.get(config.token)) {
      post('/Index/Sale/index', {}, 2).then(res => {
          this.loading = false
          if (res != null) {
            this.tableData = res.data
          }
        }, err => {
          this.loading = false
        });
    } else {
      this.$router.push({ path: '/login' })
    }
  },
  mounted: function() {
  },
  watch: {},
  computed: {},
  methods: {
      addReg: function() {
        this.$router.push({ path: '/officer'})
      },
      showReport: function() {
        this.$router.push({ path: '/report' })
      },
      cancelReg: function (hybh, sjh) {
        if (sjh) {
          this.$message({
            message: '已支付预约挂号请到柜台取消',
            type: 'warning'
          });
        } else {
          this.disabled = true
          const params = {
            hybh: hybh,
            sjh: sjh
          }
          post('/Index/Sale/cancel', params, 2).then(res => {
            this.disabled = false
            if (res != null) {
              this.reload()
              this.$message.success('预约挂号取消成功');
            }
          }, err => {
            this.disabled = false
          });    
        }
      }
  }
}
</script>

<style scoped>
.body-main {
  margin-top:1em;
  text-align: center;
}
.body-foot {
  margin-top: 2em;
  text-align: center;
}
</style>
<style>
.bodyer .reg-header-cell .cell {
    text-align: center      
}
.bodyer .reg-cell .cell {
    text-align: center    
}
</style>


