<<template>
  <div class="officer">
    <div class='liucheng'>
      <el-row :gutter="24">
        <el-steps :active="0" finish-status="success" align-center>
          <el-step title="选择科室"></el-step>
          <el-step title="选择医生"></el-step>
          <el-step title="确认挂号"></el-step>
          <el-step title="预约完成"></el-step>
        </el-steps>
      </el-row>
    </div>
    <div class='officer-main'>
      <el-row :gutter="24">
        <ul>
          <li class="keshi-li" v-for="item in tableData">
            <div class="grid-content bg-purple" @click="xzks(item.ksbm, item.ksmc)">
              <div class="keshi-name">{{item.ksmc}}</div>
              <div class="keshi-dizhi">{{item.kswz || '不详'}}</div>
            </div>
          </li>
        </ul>
      </el-row>
    </div>
  </div>
</template>

<script>
import Cookies from 'js-cookie'
import config from "@/config/deploy";
import { post, get } from "@/api/index";
export default {
  name: 'Officer',
  data() {
    return {
      tableData: [],
    }
  },
  created: function() {
    if (!Cookies.get(config.token)) {
      this.$router.push({ path: '/login' })
    }
  },
  mounted: function() {
    post('/Index/Sale/getOffices', {}, 2).then(res => {
      if (res != null) {
        this.tableData = res.data;
      }
    });
  },
  watch: {},
  computed: {},
  methods: {
    xzks: function(ksbm, ksmc) {
      this.$router.push({ path: '/doctor', query: { ksbm: ksbm, ksmc:ksmc }})
    }
  }
}
</script>

<style scoped>
.officer {
  width: 100%;
}
.liucheng {
  margin-top: 1em;
}
.officer-main {
  margin-top: 1em;
  margin: 0 auto;
  display: flex;
  justify-content: space-between
}
  .keshi-li {
    list-style-type:none;  
    margin: 0; 
    padding: 0;
    display:inline-block;
    width: 25%;
    cursor:pointer
  }
  .bg-purple {
    background: #67C23A;
  }
  .grid-content {
    margin: 2em;
    border-radius: 8px; 
    text-align: center;
    color: #ffffff;
    padding: 1em;
  }
  .keshi-name {
    font-size: 2em;
  }
  .keshi-dizhi {
    font-size: 1em;
  }
</style>


