<<template>
  <div class="payer">
    <div class='liucheng'>
      <el-row :gutter="24">
        <el-steps :active="4" finish-status="success" align-center>
          <el-step title="选择科室"></el-step>
          <el-step title="选择医生"></el-step>
          <el-step title="确认挂号"></el-step>
          <el-step title="预约完成"></el-step>
        </el-steps>
      </el-row>
    </div>
    <div class='payer-main'>
      <h1 v-if="result.status==2">预约挂号成功，请按时就诊</h1>
      <h1 v-else-if="result.status==3">预约挂号失败，挂号费将按原路返回</h1>
      <h1 v-else-if="result.status==4">挂号费返回失败，将转由人工处理</h1>
      <h1 v-else="result.status==5">预约挂号失败，挂号费已按原路返回</h1>
    </div>
  </div>
</template>

<script>
import Cookies from 'js-cookie'
import config from "@/config/deploy";
import { post, get } from "@/api/index";
export default {
  name: 'Payer',
  data() {
    return {
      result: {}
    }
  },
  created: function() {
    if (!Cookies.get(config.token)) {
      this.$router.push({ path: '/login' })
    } else {
      var tradeNo = this.$route.query.tradeNo;
      post('/Index/Sale/refresh', {tradeNo: tradeNo}, 2).then(res => {
        if (res != null) {
          this.result = res.data
          if (this.result.status == 2) {
            setTimeout(() => { 
                this.$router.push({ path: '/'})
            }, 3000);
          }
        }
      });
    }
  },
  mounted: function() {

  },
  watch: {},
  computed: {},
  methods: {}
}
</script>

<style scoped>
.payer {
  width: 100%
}
.liucheng {
  margin-top: 1em;
}
.payer-main {
  width: 100%;
  margin: 2em auto;
  text-align: center;
}
.payer-main h1 {
  font-size: 1em;
  margin-top: 4em;
}
</style>


