<<template>
  <div class="login">
    <el-card shadow="always">
      <div class="login-form" >
        <h3 >短信快捷登录</h3>
        <el-form ref="form" :model="form">
          <el-form-item>
            <el-input v-model="form.number" placeholder="请输入就诊卡号或手机号码"></el-input>
          </el-form-item>
          <el-form-item>
            <div class="login-code"><el-input v-model="form.code" placeholder="验证码"></el-input></div>
            <div class="login-send"><el-button  @click="onSend" :disabled="disabled">发送验证码</el-button></div>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" style="width:100%" @click="onSubmit" :disabled="loging">登录</el-button>
          </el-form-item>
        </el-form> 
      </div>
    </el-card>
  </div>
</template>

<script>
import Cookies from 'js-cookie'
import config from "@/config/deploy";
import { post, get } from "@/api/index";
export default {
  name: 'Login',
  data() {
    return {
      disabled: false,
      loging: false,
      form: {
          number: '',
          code: '',
        }
    }
  },
  created: function() {
    if (Cookies.get(config.token)) {
      this.$router.push({ path: '/' })
    }
  },
  mounted: function() {
  },
  destroyed: function() {},
  watch: {},
  computed: {},
  methods: {
    onSubmit: function () {
      if (this.form.number == '' || this.form.code == '' ) {
        this.$message.warning('请输入手机号码和验证码');
      } else {
        this.loging = true
        const params = {
          number: this.form.number,
          code: this.form.code,
        }
        post('/Index/Index/login', params, 2).then(res => {
          if (res != null) {
            if (res.data) {
              this.$store.state.auth = true
              this.$router.push({ path: '/' })
            } else {
              this.loging = false
              this.$message.error('账户或验证码错误');
            }
          }
        }, err => {
          this.loging = false
        });
      }
    },

    onSend: function () {
      if (this.form.number == '') {
        this.$message.warning('请输入就诊卡或手机号码');
      } else {
        this.disabled = true
        const params = {
          number: this.form.number
        }
        post('/Index/Index/getCode', params, 2).then(res => {
          this.disabled = false
          if (res != null) {
            if (res.data == true) {
              this.$message.success('验证码发送成功');
            } else {
              this.$message.warning('验证码发送失败');
            }
          }
        }, err => {
          this.disabled = false
        });
      }
    }
  }
}
</script>

<style scoped>
.login {
  width: 400px;
  margin: 0 auto;
  margin-top: 4em;
}
.login-form {
  padding: 0 1em;
}
.login-code {
  width: 200px; float: left;
  margin-right: 14px;
}
.login-send {
  width: 100px; float: left;
}
</style>


