<<template>
  <div class="order">
    <div class='liucheng'>
      <el-row :gutter="24">
        <el-steps :active="active" finish-status="success" align-center>
          <el-step title="选择科室"></el-step>
          <el-step title="选择医生"></el-step>
          <el-step title="确认挂号"></el-step>
          <el-step title="预约完成"></el-step>
        </el-steps>
      </el-row>
    </div>
    <div class='order-main'>
      <div class='order-body'>
        <el-card shadow="hover">
          <div class="order-list">
            <div class="order-li">
              <div class="bg-purple">就诊科室：</div>
              <div class="bg-purple-light">{{details.zzksmc}}</div>
            </div>
            <div class="order-li">
              <div class="bg-purple">看诊医生：</div>
              <div class="bg-purple-light">{{details.ysxm}}</div>
            </div>
            <div class="order-li">
              <div class="bg-purple">看诊时间：</div>
                <div class="bg-purple-light" v-if='details.ysh_lx == 1'>{{details.ghrq}} 上午</div>
                <div class="bg-purple-light" v-else>{{details.ghrq}} 下午</div>
            </div>
            <div class="order-li">
              <div class="bg-purple">就诊病人：</div>
              <div class="bg-purple-light">{{userinfo.xm}}</div>
            </div>
            <div class="order-li">
              <div class="bg-purple">挂号费用：</div>
              <div class="bg-purple-light">￥{{details.ghf}}</div>
            </div>
          </div>
        </el-card>  
      </div>
      <div class='order-foot'>
        <el-button type="success" @click="payOrder(2)">微信付款</el-button>
        <el-button type="primary" @click="payOrder(1)" >支付宝付款</el-button>
        <el-button @click="reset()">重选医生</el-button>
      </div>
    </div>
    <el-dialog
      width="480px"
      top="0"
      :visible.sync="payDialog"
      :before-close="handleClose" style="padding: 0">
      <div v-bind:class="['zhifu' ,payType == 1 ? 'zhifubao' : 'weixin']">
        <div class="zhifubao-info">扫码后请等待系统通知</div>
        <div class="zhifubao-jine">挂号费：￥{{details.ghf}}</div>
        <div class="zhifubao-code"><img :src="payCode" width="180" height="180" /></div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import Cookies from 'js-cookie'
import config from "@/config/deploy";
import { post, get } from "@/api/index";
export default {
  name: 'Order',
  data() {
    return {
      active: 2,
      userinfo: {},
      details: {},
      payDialog: false,
      payType: 1,
      payCode: '',
      tradeNo: '',
      timer: null,
    }
  },
  created: function() {
    if (!Cookies.get(config.token)) {
      this.$router.push({ path: '/login' })
    } else {
      post('/Index/Sale/getUser', {}, 2).then(res => {
        if (res != null) {
          this.userinfo = res.data;
        }
      });
    }
    
  },
  mounted: function() {
    this.details = this.$store.state.order;
    if (JSON.stringify(this.details) == '{}') {
      this.$router.push({ path: '/officer' })
    }
  },
  watch: {},
  computed: {},
  methods: {
    reset () {
      this.$router.go(-1)
    },
    handleClose(done) {
      this.$confirm('如果已经支付请等待系统通知...')
        .then(_ => {
          clearInterval(this.timer)
          done();
        })
        .catch(_ => {});
    },
    payOrder(zfzl) {
      this.active = 3
      const params = {
        ysbh: this.details.ysbh,
        zzks: this.details.zzks,
        ghrq: this.details.ghrq,
        ghlb: this.details.ghlb,
        ysh_lx: this.details.ysh_lx,
        ghf: this.details.ghf,
        zfzl: zfzl,
      }
      if (zfzl == 0) { // 柜台取号
        post('/Index/Sale/checkIn', params, 2).then(res => {
          if (res != null) {
            this.active = 4
            let message = '预约挂号登记成功，号源编号为' + res.data + '，以此为凭到柜台支付挂号费后即可就诊。';
            this.$alert(message, '预约登记', {
              confirmButtonText: '确定',
              callback: action => {
                this.$router.push({ path: '/' })
              }
            });
          }
        });
      } else { // 在线支付，先创建订单并生成二维码
        this.payType = zfzl
        this.payDialog = true
        this.payCode = ''
        post('/Index/Sale/createOrder', params, 2).then(res => {
          if (res != null) {
            this.payCode = res.data.codeUrl
            this.tradeNo = res.data.tradeNo
            this.timer = setInterval(() => { 
                this.refresh(this.tradeNo)
            }, 2000)
          }
        });
      }
    },
    refresh: function (tradeNo) {
      post('/Index/Sale/refresh', {tradeNo: tradeNo}, 2).then(res => {
        if (res != null) {
          if (res.data.status > 1) { // 订单支付成功
            clearInterval(this.timer)
            this.$router.push({ path: '/Payer', query: { tradeNo: tradeNo }})
          }
        }
      });
    }
  }
}
</script>

<style scoped>
.order {
  width: 100%;
}
.liucheng {
  margin-top: 1em;
}
.order-main {
  width: 100%;
  margin-top: 1em;
}
.order-body {
  font-size: 1em;
  line-height: 3em;
  width: 700px;
  margin: 0 auto;
  margin-top: 2em;
}
.order-list {
  display: flex;
  flex-wrap: wrap;
}
.order-li {
  width: 50%;
  display: flex;
}
.bg-purple {
  text-align: right;
  width: 50%
}
.bg-purple-light {
  text-align: left;
  width: 50%
}
.order-foot {
  text-align: center;
  margin-top: 2em;
}  

.zhifu {
  width:480px; height: 680px;
}
.zhifubao {
  background:url(../../static/img/zhifubao.png) no-repeat center;
}
.weixin {
  background:url(../../static/img/weixin.png) no-repeat center;
}
.zhifubao-info {
  text-align: center; padding-top: 160px; height: 2em;
  font-size: 1.5em; color: #333;
}
.zhifubao-jine {
  font-size: 1.5em; color: #FFF;  height: 2em; text-align: center;
  margin-top: 1em;
}
.zhifubao-code {
  width: 180px;
  height: 180px;
  padding-top: 42px; margin-left: 150px;
}
</style>
<style>
.liucheng .el-step .el-step__title{
  font-size: 1em;
}
.order .order-submit .el-button {
    font-size: 1em;
}
.order .el-message-box__message {
  font-size: 1em;
}
.order .el-button {
  font-size: 1em;
}
.order .el-dialog__body {
    padding: 0;
    color: #606266;
    font-size: 14px;
}
</style>


