import Vue from 'vue'
import Router from 'vue-router'
import Header from '@/components/Header'
import Footer from '@/components/Footer'
import Login from '@/components/Login'
import Bodyer from '@/components/Bodyer'
import Officer from '@/components/Office'
import Doctor from '@/components/Doctor'
import Order from '@/components/Order'
import Payer from '@/components/Payer'
import Report from '@/components/Report'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '*',
      redirect: '/'
    },
    {
      path: '/header',
      name: 'Header',
      component: Header
    },
    {
      path: '/footer',
      name: 'Footer',
      component: Footer
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/',
      name: 'Bodyer',
      component: Bodyer
    },
    {
      path: '/officer',
      name: 'Officer',
      component: Officer
    },
    {
      path: '/doctor',
      name: 'Doctor',
      component: Doctor
    },
    {
      path: '/order',
      name: 'Order',
      component: Order
    },
    {
      path: '/payer',
      name: 'Payer',
      component: Payer
    },
    {
      path: '/report',
      name: 'Report',
      component: Report
    },
  ],
  mode: 'history',
  scrollBehavior (to, from, savedPosition) {
    return { x: 0, y: 0 }
  }
})



