<template>
  <div id="app">
    <el-container>
      <el-header></el-header>
      <el-main><router-view v-if="isRouterAlive"/></el-main>
      <el-footer></el-footer>
    </el-container>
  </div>
</template>

<script>
import header from '@/components/Header'
import footer from '@/components/Footer'
export default {
  name: 'App',
  components: {
    'el-footer': footer,
    'el-header': header
  },
  provide() {
    return {
      reload: this.reload
    }
  },
  data () {
    return {
      isRouterAlive: true
    }
  }, 
  created: function () {},
  mounted: function () {},
  destroyed: function() {},
  watch: {},
  computed: {},
  methods: {
    reload() {
      this.isRouterAlive = false
      this.$nextTick(function() {
        this.isRouterAlive = true
      })
    }
  }
}
</script>

<style>
body {
  margin: 0;
  padding: 0;
  font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","微软雅黑",Arial,sans-serif;
  color: #606266;
}
#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.el-container {
  height: 100vh;
}
.el-header {
  width: 100%;
}
.el-main {
  width: 100%;
  height: 70vh;
  margin-bottom: 10vh;
}
.el-footer {
  width: 100%;
}
.el-message .el-message__content, .el-message__icon {
  font-size: 1em;
}
</style>


