<<template>
  <div class="doctor">
    <div class='liucheng'>
      <el-row :gutter="24">
        <el-steps :active="1" finish-status="success" align-center>
          <el-step title="选择科室"></el-step>
          <el-step title="选择医生"></el-step>
          <el-step title="确认挂号"></el-step>
          <el-step title="预约完成"></el-step>
        </el-steps>
      </el-row>
    </div>
    <div class='doctor-main'>
      <div class='doctor-left'>
        <div class='doctor-list'>
          <div class='doctor-li' v-for="data in tableData">
            <div class='doctor-man'>
              <div class='doctor-photo'>
                <img :src="data.photo|| doctorPhoto" />
              </div>
            </div>
            <div class='doctor-info'>
              <div class='doctor-info-top'>
                <div class='doctor-info-name'>{{data.ysxm}}</div>
                <div class='doctor-info-title'>{{data.ghlbmc}}</div>
              </div>
              <div class='doctor-info-intro'>{{data.intro || "简介待完善..."}}</div>
              <div class='doctor-info-date'>
                <div class='doctor-date-li' v-for="plan in data.plan">
                  <i class="el-icon-date"></i> <el-button type="text" 
                  @click="order(data.ysbh, data.zzks, plan.date, data.ghlb, plan.ysh_lx, data.ysxm, data.ghf, data.zzksmc)">{{plan.showDate}} {{plan.week}} {{plan.showTime}} 余号{{plan.surplus}}</el-button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class='doctor-right'>
        <template v-for="date, key in showDate">
          <div class="doctor-rlist">
            <el-button type="primary" plain @click="selectSource(key)">{{date}}</el-button>
          </div>
        </template>
      </div>
    </div>
  </div>
</template>

<script>
import Cookies from 'js-cookie'
import config from "@/config/deploy";
import { post, get } from "@/api/index";
export default {
  name: 'Doctor',
  data() {
    return {
      ksmc : '',
      ksbm: '',
      tableData: [],
      showDate: [],
      doctorPhoto: '..//..//..//static//img//doctor_male.png'
    }
  },
  created: function() {
    if (!Cookies.get(config.token)) {
      this.$router.push({ path: '/login' })
    } else {
      this.ksmc = this.$route.query.ksmc;
      this.ksbm = this.$route.query.ksbm;
      post('/Index/Sale/getSourceDate', {}, 2).then(res => {
        if (res != null) {
          this.showDate = res.data;
        }
      });
    }
  },
  mounted: function() {
    post('/Index/Sale/getSource', {ksbm: this.ksbm}, 2).then(res => {
      if (res != null) {
        this.tableData = res.data;
      }
    });
  },
  watch: {},
  computed: {},
  methods: {
    selectSource: function (appoint) {
      post('/Index/Sale/getSource', {ksbm: this.ksbm, appoint}, 2).then(res => {
        if (res != null) {
          this.tableData = res.data;
        }
      });
    },
    order: function (ysbh, zzks, ghrq, ghlb, ysh_lx, ysxm, ghf, zzksmc) {
      this.$store.state.order = {
        ysbh: ysbh,
        zzks: zzks,
        ghrq: ghrq,
        ghlb: ghlb,
        ysh_lx: ysh_lx,
        ysxm: ysxm,
        ghf: ghf,
        zzksmc: zzksmc,
      }
      this.$router.push({ path: '/order' })
    }
  }
}
</script>

<style scoped>
.doctor {
  width: 100%;
}
.liucheng {
  margin-top: 1em;
}
.doctor-main {
  display: flex;
  width: 80%;
  margin: 0 auto;
  margin-top: 1em;
  margin-bottom: 2em;
}
.doctor-left {
  width: 80%;
}

.doctor-list {
  border-right: 2px solid #ccc;
  padding-right: 2em;
  margin-right: 2em;
}
.doctor-li {
  margin-top: 2em;
  display: flex
}
.doctor-man {
  width: 200px;
  margin-right: 2em;
}
.doctor-photo {
  width: 160px;
  height: 160px;
  border-radius: 160px;
  overflow: hidden;
  box-shadow: 0 4px 4px #999;
  margin: 0 auto;
}
.doctor-photo img {
  width: 160px;
}
.doctor-info {
  flex: 1;
}
.doctor-info-top {
  display: inline-flex;
  justify-content: space-around;
}
.doctor-info-name {
  font-size: 2em;
  margin-right: 12px;
}
.doctor-info-title {
  border: 1px solid #009933;
  font-size: 12px;
  border-radius: 4px;
  height: 24px;
  color: #009933;
  margin-top: 12px;
  padding: 0 4px; 
}
.doctor-info-intro {
  color: #999;
  font-size: 12px;
  
}
.doctor-info-date {
  border-top: 1px solid #ccc;
  margin-top: 12px;
  display: block;
}
.doctor-date-li {
  border-bottom: 1px solid #ccc;
  float: left;
  width: 50%;
  padding: 0;
  font-size: 12px;
}

.doctor-right {
  width: 20%;
  padding-top: 1em;
}
.doctor-item {
  border: 1px #ccc solid;
  border-radius: 8px;
  padding: 12px;
  text-align: center;
  margin: 1em; 
  font-size: 1em;
  box-shadow: 0 1px 1px #888888;
}
.doctor-hover {
  background: #009933;
}
.doctor-rlist {
  text-align: center;
  margin-top: 1em;
}
</style>


