<template>
  <div class="header">
    <el-row >
      <el-col :span="20">
        <div class="logoer"><img src="@/assets/logo.png"/></div>
      </el-col>
      <el-col :span="4">
        <div class="handler">
          <el-button @click="shouye" round><span style="font-size: 1em;">首 页</span></el-button>
          <el-button @click="logout" round v-if="$store.state.auth"><span style="font-size: 1em;">退 出</span></el-button>
          <el-button @click="login" round v-else><span style="font-size: 1em;">登 录</span></el-button>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import config from "@/config/deploy";
import Cookies from 'js-cookie'
export default {
  name: 'Header',
  data() {
    return {
      
    }
  },
  created: function() {
    if (Cookies.get(config.token)) {
      this.$store.state.auth = true
      this.$router.push({ path: '/'})
    } else {
      this.$router.push({ path: '/login'})
    }
  },
  mounted: function() {},
  watch: {

  },
  computed: {},
  methods: {
    shouye: function () {
      this.$router.push({ path: '/' })
    },
    login: function () {
      this.$router.push({ path: '/login'})
    },
    logout: function() {
      Cookies.remove(config.token);
      this.$store.state.auth = false
      this.$store.state.token = ''
      this.$router.push({ path: '/login'})
    },
  }
}
</script>

<style scoped>
.header {
  height: 20vh;
  background: #409EFF;
  border-bottom: thin solid #DCDFE6;
}
.logoer {
  padding-top: 4vh;
  padding-left: 2ch;
}
.logoer img {
  height: 81px;
  width: 375px;
}
.handler {
  text-align: center;
  padding-top:6vh;
}
</style>


