<template>
  <div class="bodyer">
    <div class='body-main'>
      <template>
        <el-table
          :data="tableData"
          v-loading="loading"
          header-cell-class-name="reg-header-cell"
          cell-class-name="reg-cell"
          stripe
          style="width: 100%">
          <el-table-column
            prop="ghrq"
            label="检查日期">
          </el-table-column>  
          <el-table-column
            prop="mzh"
            label="门诊号">
          </el-table-column>
          <el-table-column
            prop="ysxm"
            label="看诊医生">
          </el-table-column>
          <el-table-column
            prop="byxm"
            label="就诊人">
          </el-table-column>
          <el-table-column label="报告类型">
            <template slot-scope="scope">
              <template v-if="scope.row.type=='1'">检查报告</template>
              <template v-else>检验报告</template>
            </template>
          </el-table-column>
          <el-table-column
            prop="jcxmmc"
            label="检查项目">
          </el-table-column>  
          <el-table-column label="报告详情">
            <template slot-scope="scope">
              <template v-if="scope.row.type=='1'"><el-button type="success" round @click="showCheck(scope.row)">查看</el-button></template>
              <template v-else><el-button type="success" round @click="showTest(scope.row)">查看</el-button></template>
            </template>
          </el-table-column>
        </el-table>
      </template>
    </div>
    <div class='body-foot'>
      <el-dialog title="检查结果" :visible.sync="dialogCheckVisible">
        <div class="dialog_check">
          <div class="dialog_check_list">
            <div class="dialog_check_title">检查编号:</div>
            <div class="dialog_check_content">{{gridData.kdxh|| "--"}}</div>
          </div>
          <div class="dialog_check_list">
            <div class="dialog_check_title">检查名称:</div>
            <div class="dialog_check_content">{{gridData.jcxmmc|| "--"}}</div>
          </div>
          <div class="dialog_check_list">
            <div class="dialog_check_title">检查部位:</div>
            <div class="dialog_check_content">{{gridData.jcjg.jcbw|| "--"}}</div>
          </div>
          <div class="dialog_check_list">
            <div class="dialog_check_title">检查方式:</div>
            <div class="dialog_check_content">{{gridData.jcjg.jcfs|| "--"}}</div>
          </div>
          <div class="dialog_check_list">
            <div class="dialog_check_title">检查结果:</div>
            <div class="dialog_check_content">{{gridData.jcjg.jg|| "--"}}</div>
          </div>
          <div class="dialog_check_list">
            <div class="dialog_check_title">诊断意见:</div>
            <div class="dialog_check_content">{{gridData.jcjg.zd|| "--"}}</div>
          </div>
          <div class="dialog_check_list">
            <div class="dialog_check_title">影像所见:</div>
            <div class="dialog_check_content">{{gridData.jcjg.zdnr|| "--"}}</div>
          </div>
        </div>  
      </el-dialog>
      <el-dialog title="检验报告" :visible.sync="dialogTestVisible">
        <div v-if="testData.jyjg.length > 0">
          <el-table
            :data="testData.jyjg"
            style="width: 100%">
            <el-table-column
              prop="SAMPLE_GROUP_NAME"
              label="检验项目">
            </el-table-column>
            <el-table-column
              prop="ITEMNAME"
              label="指标名称">
            </el-table-column>
            <el-table-column
              prop="RESULT"
              label="检验结果">
            </el-table-column> 
            <el-table-column
              prop="UNIT"
              label="单位">
            </el-table-column>
            <el-table-column
              prop="REFRANGE"
              label="参考范围">
            </el-table-column> 
          </el-table>
        </div>
        <div v-if="testData.shjg.length > 0">
          <el-table
            :data="testData.shjg"
            style="width: 100%">
            <el-table-column
              prop="jcxm"
              label="检验名称">
            </el-table-column>
            <el-table-column
              prop="xjmc"
              label="细菌名称">
            </el-table-column>
            <el-table-column
              prop="antiname"
              label="抗生素名称">
            </el-table-column> 
            <el-table-column
              prop="suscept"
              label="药敏结果">
            </el-table-column>
            <el-table-column
              prop="susQuan"
              label="药敏数量">
            </el-table-column> 
            <el-table-column
              prop="refRange"
              label="查考范围">
            </el-table-column> 
          </el-table>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import Cookies from 'js-cookie'
import config from "@/config/deploy";
import { post, get } from "@/api/index";
export default {
  inject: ['reload'],
  name: 'Bodyer',
  data() {
    return {
      loading: true,
      disabled: false,
      tableData: [],
      gridData: {
        "jcjg" : {}
      },
      dialogCheckVisible: false,
      dialogTestVisible: false,
      testData: {
        "jyjg" : [],
        "shjg" : [],
      }
    }
  },
  created: function() {
    if (Cookies.get(config.token)) {
      post('/Index/Sale/report', {}, 2).then(res => {
          this.loading = false
          if (res != null) {
            this.tableData = res.data
          }
        }, err => {
          this.loading = false
        });
    } else {
      this.$router.push({ path: '/login' })
    }
  },
  mounted: function() {
  },
  watch: {},
  computed: {},
  methods: {
    showCheck: function(data) {
      this.dialogCheckVisible = true
      this.gridData = data
    },
    showTest: function(data) {
      console.log(data)
      this.dialogTestVisible = true
      this.testData = data
    }
  }
}
</script>

<style scoped>
.body-main {
  margin-top:1em;
  text-align: center;
}
.body-foot {
  margin-top: 2em;
  text-align: center;
}
.dialog_check {
  text-align: left;
  font-size: 14px;
  padding: 0  40px 20px 40px;
}
.dialog_check_list {
  display: flex;
  margin-top: 20px;
}
.dialog_check_title {
  font-weight: bold;
  width: 80px;
}
</style>
<style>
.bodyer .reg-header-cell .cell {
    text-align: center      
}
.bodyer .reg-cell .cell {
    text-align: center    
}
</style>


